* {
    font-family: Helvetica;
    body {
        padding: 60px;
        input {
            color: grey;
            font-size: 1.2rem;
            width: 100%;
        }
        .card-grid {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            .card {
                align-content: center;
                border: 1px solid grey;
                border-radius: 6px;
                overflow: hidden;
                margin-bottom: 10px;
                margin-right: 10px;
                display: inline-block;
                p {
                    padding: 5px;
                    text-align: center;
                }
                img {
                    max-height: 170px;
                }
            }
        }
    }
}
